<template>
  <div class="login-box" style="text-align: center">
    <div>
      账号：
      <Input type="text" v-model="user.username" style="width: 86%" />
    </div>
    <div style="margin-top: 10px;margin-bottom: 10px">
      密码：
      <Input type="password" v-model="user.password" style="width: 86%" />
    </div>
    <Button type="primary" size="small" style="margin-right: 5px" @click="_login">登录</Button>
  </div>
</template>

<script>
import { Http_Post } from "../common/Http";
import { loginPost } from "../params/Params";

export default {
  data() {
    return {
      formLabelWidth: "120px",
      user: {
        username: "",
        password: ""
      }
    };
  },
  methods: {
    _login() {
      let _this = this;
      Http_Post(
        loginPost(_this.user),
        function(datas) {
          if (datas.msg === 0) {
            sessionStorage.setItem("user", JSON.stringify(datas.data));
            localStorage.setItem("username", _this.user.username);
            localStorage.setItem("password", _this.user.password);
            _this.$router.push({
              name: "home"
            });
          } else {
            _this.$message.error(datas.error);
          }
        },
        function(error) {
          _this.$message.error(error);
        }
      );
    }
  },
  created() {
    this.user.username = localStorage.getItem("username");
    this.user.password = localStorage.getItem("password");
    sessionStorage.removeItem("user");
  }
};
</script>

<style scoped>
.login-box {
  width: 350px;
  margin: 50ch auto;
}

.login-title {
  text-align: center;
  margin: 0 auto 40px auto;
  color: #303133;
}
</style>
