
//-------------------------------------------------------------------------
// 添加订单
export function addOrder(orderData) {
  let param = new URLSearchParams();
  param.append("ac", "add_order");
  param.append("customerCode", orderData.customerCode);
  param.append("num", orderData.num);
  param.append("price", orderData.price);
  param.append("orderItemListJson", orderData.orderItemListJson);
  return param
}

// 查找订单
export function findOrder(shop = "", state = 0, page = 0, size = 9999) {
  let param = new URLSearchParams();
  param.append("ac", "find_order");
  param.append("page", page.toString());
  param.append("size", size.toString());
  param.append("shop", shop);
  param.append("state", state.toString());
  return param
}

// 删除订单
export function deleteOrder(code) {
  let param = new URLSearchParams();
  param.append("ac", "delete_order");
  param.append("code", code);
  return param
}

// 修改订单
export function updateOrder(orderData) {
  let param = new URLSearchParams();
  param.append("ac", "update_order");
  param.append("code", orderData.code);
  param.append("customerCode", orderData.customerCode);
  param.append("num", orderData.num);
  param.append("price", orderData.price);
  param.append("state", orderData.state);
  param.append("orderItemListJson", orderData.orderItemListJson);
  return param
}

// 查找订单详情
export function findOrderItem(code) {
  let param = new URLSearchParams();
  param.append("ac", "find_order_item");
  param.append("code", code);
  return param
}

//------------------------------------------------------------------------
// 登录
export function loginPost(user) {
  return {
    ac: 'login',
    username: user.username,
    password: user.password
  }
}
